<template>
  <div class="center questionnaireLibrary">
      <div class="center_breadcrumb">
        <crumb></crumb>
    </div>
    <div class="center_content">
                <div class="questionnaireLibrary-course">
                        <div class="center_content_search">
                            <div class="center_content_search_left">
                                <div class="search-input-suffix">
                                    <label>问卷名称：</label>
                                    <el-input class="search"
                                            @keyup.enter.native="search"
                                            v-model="page.keyWord"
                                            placeholder="请输入搜索关键字"
                                            >
                                    </el-input>
                                </div>
                            <div class="search--btn" style="margin: 0 0 0 20px" @click="search">搜索</div>
                            </div>
                        </div>
                        <el-table :data="list"  stripe style="width: 100%; padding-bottom:40px;border-radius: 10px;">
                           <el-table-column label="问卷名称"   :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    {{ scope.row.formName}} 
                                </template>
                            </el-table-column>
                            <el-table-column label="引用次数" >
                                <template slot-scope="scope">
                                    <span>{{ scope.row.refCount }}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="220">
                                <template slot-scope="scope">
                                    <el-button type="text" size="small" @click="openPreview(scope.row.id)">
                                        <svgIcon icon-class="iconyulan"></svgIcon>&nbsp;&nbsp;预览
                                    </el-button>
                                     <el-button type="text" size="small" @click="openQuote(scope.row)">
                                        <svgIcon icon-class="iconyinyong"></svgIcon>&nbsp;&nbsp;引用
                                    </el-button>
                                     <el-button style="color: #ff5858;" type="text" size="small" @click="discard(scope.row.id)">
                                        <svgIcon icon-class="iconshanchu"></svgIcon>&nbsp;&nbsp;废弃
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                </div>
    </div>
 </div>
</template>

<script>
export default {
 name: 'CourseLibrary',
 components: {},
 data() {
 return {
    pageTitle:'课程模板库',
    activeNum:0,
    list:[],
    page:{
      keyWord:''  
    },
    api:{
        templateList:'/form/rest/FormTemplateMainBaseBusiness/listCourseAll/v1',
        discardTemp:'/form/rest/FormMainBaseBusiness/discardTemp/v1'
    }
 }
 },
 created(){
   this.initData()
 },
 mounted(){},
 methods:{
     initData(){
         this.$post(this.api.templateList, this.page).then(res =>{
             if(res.code ===200){
                 res.data.forEach(item =>{
                     item.off=true
                     item.on=true
                 })
                 this.list= res.data
             }
         })
     },
     search(){
         this.initData()
     },
     openPreview(id){
         this.$router.push({
            path: "/courseQualityEvaluation/coursePreview",
            query: {
            entryType: "template",
            id: id
            }
         });
     },
     openQuote(vo){
          this.$router.push({
            path: "/courseQualityEvaluation/NewQuestionnaire",
            query: {
            type: "course",
            status: "edit",
            point:'template',
            id: vo.id
            }
         });
     },
     discard(id){
        this.$post(this.api.discardTemp, {id:id}).then(res =>{
             if(res.code ===200){
                this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '操作成功'                     
                    })

                this.initData()
             }else{
                 this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.msg
               }) 
             }
         })
     },
     enter(type, index, off){
        if(type === 'preview'){
             this.list[index].off= !off
         }else{
            this.list[index].on= !off
         }
       
     },
     leave(type, index, off){
     if(type === 'preview'){
             this.list[index].off= !off
         }else{
            this.list[index].on= !off
         }
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
  .questionnaireLibrary{
        .active{
            background-color: #5f77ff;
            color: #fff;
            border-radius: 4px;
            border:0;
        }
        .questionnaireLibrary-course{
            width:100%;
            overflow-y: auto;
            margin-right:20px;
        }
        .el-pagination{
            margin:20px 0;
            float: right;
        }
        
    }

</style>




